import { useCoverContext, useToolbarContext } from 'contexts';
import {
  ToolbarSearch,
  ToolbarShare,
  ToolbarConfig,
  ToolbarIcon,
  ToolbarTooltip,
} from '.';
import { ToolConfig, ToolConfigIDs } from 'types';

export const Toolbar: React.FC = () => {
  const { erase, setErase, editLines, setEditLines } = useCoverContext();
  const {
    openSearch,
    setOpenSearch,
    openResize,
    setOpenResize,
    openShare,
    setOpenShare,
  } = useToolbarContext();

  const configTools: Array<ToolConfig> = [
    {
      id: ToolConfigIDs.SEARCH,
      tooltip: 'Add new album',
      color: 'green',
      emoji: '🔍',
      value: openSearch,
      valueModifier: setOpenSearch,
      reverse: true,
    },
    {
      id: ToolConfigIDs.RESIZE,
      tooltip: 'Resize elements',
      color: 'purple',
      emoji: '🧰',
      value: openResize,
      valueModifier: setOpenResize,
      reverse: true,
    },
    {
      id: ToolConfigIDs.SHARE,
      tooltip: 'Share Options',
      color: 'orange',
      emoji: '🔗',
      value: openShare,
      valueModifier: setOpenShare,
      reverse: true,
    },
    {
      id: ToolConfigIDs.ARROW,
      tooltip: 'Arrow creation mode',
      color: 'yellow',
      emoji: '➜',
      value: editLines,
      valueModifier: setEditLines,
      reverse: false,
    },
    {
      id: ToolConfigIDs.ERASE,
      tooltip: 'Delete mode',
      color: 'red',
      emoji: '🗑️',
      value: erase,
      valueModifier: setErase,
      reverse: false,
    },
  ];

  return (
    <>
      <ToolbarSearch />
      <ToolbarConfig />
      <ToolbarShare />
      {configTools.map((config, index) => (
        <ToolbarIcon config={config} key={config.id} index={index} />
      ))}
      <ToolbarTooltip />
    </>
  );
};
